import React from 'react';
import './Contents.css'
import About from './page/About'
function Contents(props){
    let page;
    switch(props.cur){
        case 1:
            page = <About />
            break;
        case 2:
            page = <div className="content">ihih</div>
            break;
        case 3:
            page = <div className="content">ihih</div>

    }
    return(
        <div className="content-wrapper">
           <div className="content">{page}</div>
        </div>
    )
}
export default Contents;