import React from 'react'
import './About.css'
function About() {
    return(
        <div>
            <div className="wrapper">
                <h1 id="id1">이 동아리는 FORIF 입니다!</h1>
                <p>앞에서 살펴본 바와 같이 일정금액을 같은 금리의 단리와 복리로 적용했을 때 시간이 흘러 발생하는 원리금에는 차이
                가 발생한다. 복리는 투자기간이 길어질수록 원리금이 기하급수적으로 증가하며 위력을 발휘하게 된다. 다음 그림은
                100만원을 연 4%의 수익률로 투자한 경우 10년 단위로 기간이 길어질수록 투자자산이 증가하는 모습을 보여주고 있
                다.</p>
            </div>
        </div>
    )
}
export default About;